<template>
    <div class="address">
        <van-nav-bar title="地址" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <van-address-list
            v-model="aid"
            :list="addressList"
            default-tag-text="默认"
            @add="onAdd"
            @edit="onEdit"
        />
    </div>
</template>

<script>
    import { Toast } from "vant";

    export default {
        data() {
            return {
                aid: null,
                addressList: [],
            };
        },

        methods: {
            // 获取地址信息
            getAddress() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAddress",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    },
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 20000) {
                        res.data.result.forEach(item => {
                            let obj = {
                                id: item.aid,
                                name: item.name,
                                tel: item.tel,
                                address: item.province + item.city + item.county + item.addressDetail,
                                isDefault: Boolean(item.isDefault),
                            }

                            if(item.isDefault) {
                                this.aid = item.aid;
                            }

                            this.addressList.push(obj);
                        });
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 新增地址
            onAdd() {
                // 跳转到新增地址
                this.$router.push({ name: "NewAddress" });
            },

            // 修改地址
            onEdit(item) {

                this.$router.push({
                    path: "/editAddress",
                    query: {
                        id: item.id,
                    }
                });
            }
        },

        created() {
            // 获取地址信息
            this.getAddress();
        }
    }
</script>

<style lang="less" scoped>
    .van-nav-bar {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 45px;
    }

    .van-address-list{
        margin-top: 45px;
    }
</style>